/* Color variables and root styles */
:root {
  --accent: #3b82f6;
  --text-primary: #1f2937;
  --text-secondary: #6b7280;
  --text-muted: #9ca3af;
  --border-light: #e5e7eb;
  --border-focus: var(--accent);
  --bg-white: #ffffff;
  --shadow-md: 0 4px 6px rgba(0,0,0,0.1);
  --radius-md: 6px;
}

body {
  background: var(--bg-white);
  font-family: -apple-system,BlinkMacSystemFont,'Segoe UI',Roboto,'Helvetica Neue',Arial,sans-serif;
}

.app-root {
  min-height: 100vh;
  background: var(--bg-white);
}

.navbar {
  background: var(--accent);
  color: #fff;
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.navbar-brand {
  font-weight: 700;
  font-size: 1.3rem;
}
.navbar-links {
  display: flex;
  gap: 2rem;
  list-style: none;
}
.navbar-links li a {
  color: #fff;
  text-decoration: none;
  font-weight: 600;
}

.main-content {
  padding: 2rem 0 1rem 0;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.event-list-container,
.event-details-container,
.dashboard-container {
  max-width: 600px;
  margin: 2rem auto;
  background: #fff;
  border: 1px solid var(--border-light);
  box-shadow: var(--shadow-md);
  padding: 2rem;
  border-radius: var(--radius-md);
}
.events-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  margin: 2rem 0 1rem 0;
}
.event-card {
  box-shadow: var(--shadow-md);
  padding: 1rem;
  border-radius: var(--radius-md);
  background: var(--bg-white);
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  border: 1px solid var(--border-light);
}
.btn-primary {
  background: var(--accent);
  color: white;
  border: 1px solid var(--accent);
  padding: 0.5rem 1rem;
  border-radius: var(--radius-md);
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  margin-top: 0.5rem;
}
.btn-primary:disabled {
  background: #ccc;
  color: #fff;
  cursor: not-allowed;
}
.pagination-bar {
  display: flex;
  gap: 1rem;
  align-items: center;
  justify-content: center;
  margin-top: 1.5rem;
}
label {
  display: block;
  margin-bottom: .25rem;
  font-size: 1rem;
}
select, input[type="text"] {
  width: 100%;
  padding: .5rem;
  margin-bottom: 1rem;
  border: 1px solid var(--border-light);
  border-radius: var(--radius-md);
  font-size: 1rem;
}
input:focus, select:focus {
  border-color: var(--accent);
  outline: none;
}
.error {
  color: #dc2626;
  background: #fbe9e9;
  border: 1px solid #ef4444;
  padding: .75rem;
  border-radius: var(--radius-md);
  margin-top: .75rem;
}
.success {
  color: #2a933c;
  background: #eaffea;
  border: 1px solid #34d64e;
  padding: .7rem;
  border-radius: var(--radius-md);
  margin-top: .6rem;
}
.empty-state {
  text-align: center;
  color: var(--text-muted);
  font-style: italic;
  margin: 1.2rem 0;
}
.registration-form {
  margin-top: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.student-form {
  margin-bottom: 2rem;
  display: flex;
  flex-direction: row;
  gap: 1rem;
}
.registration-list, .dashboard-registration-item {
  padding: 0;
  list-style: none;
}
.dashboard-registration-item {
  border-bottom: 1px solid var(--border-light);
  padding: 0.5rem 0;
  font-size: 1rem;
}
@media (max-width: 360px) {
  .event-list-container,.event-details-container,.dashboard-container {
    padding: 1rem;
    margin: 0.5rem;
  }
  .events-grid {
    grid-template-columns: 1fr;
    gap: 0.7rem;
  }
}
